<template>
  <div class="register" v-show="isShow">            
    <div class="bg">
      <img src="/static/img/register.png" alt=""> 
    </div>
    <div class="box">
      <div>
        <img src="/static/img/phone.png" alt="">  
      </div>    
      <input type="text" 
      class="mobile" 
      placeholder="请输入手机号码" 
      v-model="phoneNum" 
      @focus="clearPhoneNum"
      @blur.prevent="checkPhoneNum"
      v-focus>
    </div>

    <div class="divide">
      {{msgForPhone}}
    </div>

    <div class="box2">
      <div>
        <img src="/static/img/yzm.png" alt="">
      </div>      
      <input type="text" 
      class="verification" 
      placeholder="验证码" 
      v-model="verifyNum"
      @focus="clearVerifyNum">

      <div class="getCode" @click="checkVerifyNum">获取验证码</div>
    </div> 

    <div class="divide">
      {{msgForVerify}}
    </div>

    <div class="apply" @click="checkApply">注册</div> 
    <div class="divide">
      {{msgForaApply}}
    </div>
  </div>
</template>

<script>
  import localStorageUtils from '@/utils/localStorageUtils'

  export default {
    data () {
      return {
        isShow: false,
        phoneNum: '',
        msgForPhone: '',
        isRightPhoneNum: false,
        verifyNum: '',
        msgForVerify: '',
        isRightVerifyNum: false,
        msgForaApply: ''
      }
    },
    created() {
      this.checkRegister()
    },           
    methods:{
      checkRegister(){
        //判断是否为微信浏览器
        var isWeixin = this.$options.methods.isWeixn()

        //判断用户是否已注册
        var registerArr = localStorageUtils.read()
        var isRegister = true

        if (registerArr.length === 0){
          isRegister = false
        }

        if (isWeixin || isRegister){
          this.$router.push('/')
          return
        }

        this.isShow = true
      },
      isWeixn(){
        var ua = navigator.userAgent.toLowerCase()
        if(ua.match(/MicroMessenger/i)=="micromessenger") {
          return true
        } else {
          return false
        }
      },      
      checkPhoneNum(){
        this.isRightPhoneNum = false

        var reg=11&& /^((13|14|15|17|18)[0-9]{1}\d{8})$/ //手机号正则验证
        var phoneNum = this.phoneNum

        if(!phoneNum){//未输入手机号
          this.msgForPhone = '请输入手机号码'
          return
        }

        if(!reg.test(phoneNum)){//手机号不合法
          this.msgForPhone = '您输入的手机号码不合法，请重新输入'
          return
        }

        this.isRightPhoneNum = true
      },
      clearPhoneNum(){
        this.msgForPhone = ''
        this.msgForaApply = ''
      },
      checkVerifyNum(){ // TODO
        this.isRightVerifyNum = false

        if(!this.isRightPhoneNum){
          this.msgForVerify = '请先输入正确的手机号码'
          return
        }

        this.isRightVerifyNum = true
      }, 
      clearVerifyNum(){
        this.msgForVerify = ''
        this.msgForaApply = ''
      },
      checkApply(){

        if (!this.isRightPhoneNum || !this.isRightVerifyNum){
          this.msgForaApply = '请先输入正确的手机号码与验证码'
          return
        }

        var arr = []
        arr.push({phoneNum:this.phoneNum})

        localStorageUtils.save(arr)
      }           
    }
  }
</script>


<style  lang="scss" scoped>
  .register{
    width: auto;
    height: 667px;
    background: #FFFFFF;
  }

  .bg{
    width: auto;
    height: 330px;

    img{
      width: 100%;
      height: 100%;
    }
  }

  .box{
    width: auto;
    height: 42px;
    margin: 0px 53px;
    border:1px solid rgba(71, 178, 240, 1);
    border-radius:4px;
    display: flex;

    div{
      width: 26px;
      height: 26px; 
      margin: 7px 21px;

      img{
        width: 26px;
        height: 26px;
      }    
    }

    .mobile{
      border: none;
    }

    input{
      width: auto;
      height: 100%;
      // 去掉Chrome浏览器中输入框以及其它表单控件获得焦点时的带颜色边框
      outline:none;
    }

    input::-webkit-input-placeholder{
      color: #90D0F4;
      font-size: 12px;
      text-align: left;
    }
  }

  .box2{
    width: auto;
    height: 42px;
    margin: 0px 53px;
    border:1px solid rgba(71, 178, 240, 1);
    border-radius:4px;
    display: flex;
    position: relative;

    div{
      width: 26px;
      height: 26px; 
      margin: 7px 21px;

      img{
        width: 26px;
        height: 26px;
      }    
    }

    .verification{
      border: none;      
    }

    input{
      width: auto;
      height: 100%;
      outline:none;
    }

    input::-webkit-input-placeholder{
      color: #90D0F4;
      font-size: 12px;
      text-align: left;
    }     

    .getCode{
      width: 70px;
      height: 28px;
      background:rgba(71,178,240,1);
      position: absolute;
      z-index: 88;
      right: 7px;
      top: 7px;
      margin: 0px;

      font-size:13px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(255,255,255,1);
      line-height:27px;
      text-align: center;
    }
  }

  .divide{
    width: auto;
    height: 21px;
    font-size:13px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:red;
    line-height:38px;

    display: flex;
    justify-content: center;
    align-items: center;
  }

  .apply{
    width: auto;
    height: 42px;
    margin: 10px 53px 10px 53px;
    background:rgba(71,178,240,1);
    border-radius:41px;

    font-size:18px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:rgba(255,255,255,1);
    line-height:38px;

    display: flex;
    justify-content: center;
    align-items: center;

  }   
</style>



